// GenreMenu.jsx
import { h } from 'preact';

const GenreMenu = ({ genres, selectedGenre, onSelect }) => (
  <div className="menu genre-menu" role="navigation" aria-label="Genres">
    <h2 className="sticky-header">GENRE</h2>
    <ul>
      {genres.map((genre) => (
        <li
          key={genre}
          className={genre === selectedGenre ? 'active' : ''}
          onClick={() => onSelect(genre)}
          tabIndex="0"
          onKeyPress={(e) => {
            if (e.key === 'Enter') onSelect(genre);
          }}
        >
          {genre}
        </li>
      ))}
    </ul>
  </div>
);

export default GenreMenu;


