// StationMenu.jsx
import { h } from 'preact';

const StationMenu = ({ stations, selectedStation, onSelect, genre }) => (
  <div className="menu station-menu" role="navigation" aria-label={`${genre} Stations`}>
    <h2 className="sticky-header"> STATIONS</h2>
    <ul>
      {stations.map((station) => (
        <li
          key={station.videoId}
          className={station.videoId === (selectedStation && selectedStation.videoId) ? 'active' : ''}
          onClick={() => onSelect(station)}
          tabIndex="0"
          onKeyPress={(e) => {
            if (e.key === 'Enter') onSelect(station);
          }}
        >
            {station.name}
        </li>
      ))}
    </ul>
  </div>
);

export default StationMenu;
