// src/components/App.jsx
import { h } from 'preact';
import { useEffect, useState, useRef } from 'preact/hooks';
import stationData from '../assets/videoId.json';
import gifs from '../assets/gifs.json';

import GenreMenu from './GenreMenu';
import StationMenu from './StationMenu';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCirclePlay, faCirclePause } from "@fortawesome/free-regular-svg-icons";

const App = () => {
  const defaultStation = { name: 'LOFI GIRL', videoId: 'jfKfPfyJRdk' };
  const [isPlaying, setIsPlaying] = useState(false);
  const [playerReady, setPlayerReady] = useState(false);
  const [currentGif, setCurrentGif] = useState("");
  const [selectedGenre, setSelectedGenre] = useState(() => {
    const storedGenre = localStorage.getItem('selectedGenre');
    return storedGenre ? storedGenre.toUpperCase() : 'LOFI';
  });
  const [selectedStation, setSelectedStation] = useState(() => {
    const station = localStorage.getItem('selectedStation');
    return station ? JSON.parse(station) : defaultStation;
  });
  const [volume, setVolume] = useState(() => {
    const savedVolume = localStorage.getItem('volume');
    return savedVolume ? parseInt(savedVolume, 10) : 50;
  });
  const playerRef = useRef(null);

  // Initialize YouTube Player once the API is ready
  useEffect(() => {
    const initializePlayer = () => {
      playerRef.current = new window.YT.Player('player', {
        videoId: selectedStation.videoId,
        events: {
          onReady: () => {
            console.log('YouTube Player is ready.');
            setPlayerReady(true);
            setIsPlaying(true); // Assuming it starts playing automatically
            playerRef.current.setVolume(volume);
          },
          onStateChange: (event) => {
            if (event.data === window.YT.PlayerState.PLAYING) {
              setIsPlaying(true);
            } else {
              setIsPlaying(false);
            }
          },
          onError: (event) => {
            console.error('YouTube Player error:', event);
          },
        },
      });
    };

    if (window.YT && window.YT.Player) {
      initializePlayer();
    } else {
      // Ensure the API script is loaded before initializing
      window.onYouTubeIframeAPIReady = initializePlayer;
    }

    // Cleanup on unmount
    return () => {
      if (playerRef.current) {
        playerRef.current.destroy();
        console.log('YouTube Player destroyed.');
      }
    };
  }, []);

  // Update video when selectedStation changes
  useEffect(() => {
    if (playerReady && playerRef.current && selectedStation) {
      playerRef.current.loadVideoById(selectedStation.videoId);
      // isPlaying will be updated via onStateChange
    }
  }, [selectedStation, playerReady]);

  // Update volume when volume state changes
  useEffect(() => {
    if (playerReady && playerRef.current) {
      playerRef.current.setVolume(volume);
    }
    localStorage.setItem('volume', volume);
  }, [volume, playerReady]);

  // Function to randomly select a GIF
  const selectRandomGif = () => {
    if (gifs.gifs && gifs.gifs.length > 0) {
      const randomIndex = Math.floor(Math.random() * gifs.gifs.length);
      setCurrentGif(gifs.gifs[randomIndex]);
    } else {
      console.error('No GIFs available in gifs.json');
    }
  };

  // Trigger random GIF selection when the station changes
  useEffect(() => {
    if (selectedStation) {
      selectRandomGif();
    }
  }, [selectedStation]);

  // Handle Playback Toggle
  const togglePlayback = () => {
    if (!playerReady || !playerRef.current) {
      console.error('Player not initialized or not ready');
      return;
    }

    if (isPlaying) {
      playerRef.current.pauseVideo();
    } else {
      playerRef.current.playVideo();
    }
  };

  // Handle Genre Selection
  const handleGenreSelect = (genre) => {
    const upperGenre = genre.toUpperCase();
    setSelectedGenre(upperGenre);
    localStorage.setItem('selectedGenre', upperGenre);
    // Do not change the selectedStation
    // User can explore stations without interrupting playback
  };

  // Handle Station Selection
  const handleStationSelect = (station) => {
    setSelectedStation(station);
    localStorage.setItem('selectedStation', JSON.stringify(station));
  };

  // Extract genres from JSON
  const genres = Array.isArray(stationData.stations)
    ? stationData.stations.map((genreObj) => Object.keys(genreObj)[0].toUpperCase())
    : [];

  // Get stations for the selected genre
  const stations = selectedGenre
    ? stationData.stations.find(
        (genreObj) => Object.keys(genreObj)[0].toUpperCase() === selectedGenre
      )?.[selectedGenre] || []
    : [];

  // Debugging logs
  console.log('Station Data:', stationData);
  console.log('Selected Genre:', selectedGenre);
  console.log('Stations:', stations);

  return (
    <div className="app-container">
      <div className="content">
        {/* Genre Menu */}
        <GenreMenu
          genres={genres}
          selectedGenre={selectedGenre}
          onSelect={handleGenreSelect}
        />

        {/* Station Menu */}
        {selectedGenre && (
          <StationMenu
            stations={stations}
            selectedStation={selectedStation}
            onSelect={handleStationSelect}
            genre={selectedGenre}
          />
        )}
      </div>
      {/* YouTube Player and Controls */}
      <div className="player-section">
        {/* Display current station name */}
        <div className="retro-container">
          <h2>{selectedStation ? selectedStation.name.toUpperCase() : 'Select a station to play'}</h2>
        </div>

        {/* Hidden player to stream audio */}
        <div id="player" style={{ display: 'none' }}></div>
        {/* Play/Pause Button */}
        <div
          className="button-area"
          style={{
            backgroundImage: isPlaying && currentGif ? `url(${currentGif})` : "none",
          }}
        >
          <button
            onClick={togglePlayback}
            disabled={!playerReady}
            aria-label={isPlaying ? "Pause music" : "Play music"}
            className={`play-button ${isPlaying ? "pause" : "play"}`}
          >
            {playerReady ? (
              <FontAwesomeIcon icon={isPlaying ? faCirclePause : faCirclePlay} />
            ) : (
              <span className="spinner"></span>
            )}
          </button>
        </div>

        {/* Volume Slider */}
        <div className="volume-control">
          <label htmlFor="volume-slider">Volume: {volume}%</label>
          <div className="volume-line">
            <input
              type="range"
              id="volume-slider"
              min="0"
              max="100"
              value={volume}
              onChange={(e) => setVolume(parseInt(e.target.value, 10))}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default App;
